import React, { Component } from 'react';
import { connect } from 'react-redux';
import currencyFormatter from 'currency-formatter';
import * as actions from '../../state/actions/index';

const TransactionList = (props) => {

    var rowSum = 0;
    const rowsData = props.data.map(function (dataItem) {
        rowSum = rowSum + dataItem.Amount;
        return (
            <tr key={dataItem.id}>
                <td>{dataItem.recentActivity}</td>
                <td>{dataItem.type}</td>
                <td>{dataItem.Description}</td>
                <td>{currencyFormatter.format(dataItem.Amount, { code: 'USD' })}</td>
            </tr>
        );
    }
    );
    return (

        <table className="table borderless DecisionQuestionApp__table" id="myTable" >
            <tbody>
                <tr>
                    <td><strong>Date</strong></td>
                    <td><strong>Type</strong></td>
                    <td><strong>Description</strong></td>
                    <td><strong>Amount</strong></td>
                </tr>
                {rowsData}
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td><hr className="DecisionQuestionApp__hr" /></td>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td className="DecisionQuestionApp__total">Total:&nbsp;&nbsp;&nbsp;&nbsp;${rowSum}</td>
                </tr>
            </tbody>
        </table>
    );
}

class DisputeCreditTransaction extends Component {

    componentDidMount = () => {
        const transactionDisputes = localStorage.getItem("transactionDisputes");
        console.log(transactionDisputes);

        if (transactionDisputes) {
            const transactionDisputesjs = JSON.parse(transactionDisputes);
            this.props.addDispute(transactionDisputesjs);
        }
        
        const accessToken = localStorage.getItem("accessToken");
        // const accessToken = "7a84e1b9f19b3d68b826b737d9edc918";

        if (accessToken) {
            console.log(accessToken);
            this.props.remoteGetUserProfile(accessToken);
        }
    }
    state = {
        confirmCheck: false
    }

    confirmed = () => {
        var jsonObj = this.state;
        jsonObj.confirmCheck = true;
        this.setState({
            jsonObj
        });
    }
    cancelHandler = () => {
        window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-customer/en/landing_secure.page';
    }

    render() {
        return (
            <div>
                <div className="row" >
                    <br />
                    <div className="col-sm-12">
                        <p className="DecisionQuestionApp__question">Please confirm the following transactions in question</p>
                        <label className="DecisionQuestionApp__step"><b>Transaction selected:</b></label>
                        <TransactionList data={this.props.transactionDisputes} />
                    </div>
                </div>
                <div className="row">
                    <div className="DecisionQuestionApp__spacer">
                        <button name="confirm" type="button" className="btn DecisionQuestionApp__custom-buttons btn-primary" onClick={() => this.confirmed()} >Yes - This is correct</button>
                        <button name="cancel" type="button" className="btn DecisionQuestionApp__custom-buttons btn-default " onClick={() => this.cancelHandler()} >No - Cancel</button>
                    </div>
                </div>
                <div className="row">
                    {
                        this.state.confirmCheck === true ?
                            <button name="next" type="button" className="btn btn-primary pull-right stepZillRightabt" onClick={() => this.props.jumpToStep(1)} >
                                Next</button>
                            :
                            <button name="next" disabled type="button" className="btn btn-default pull-right stepZillRightabt" onClick={() => this.props.jumpToStep(1)} >
                                Next</button>
                    }
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        transactionDisputes: state.transactionDisputeReducer.transactionDisputes
    }
}

const mapDispatchToProps = dispatch => {
    return {
        addDispute: (data) => dispatch(actions.addDispute(data)),
        remoteGetUserProfile: (accessToken) => dispatch(actions.remoteGetUserProfile(accessToken)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DisputeCreditTransaction);



